.btn {
  display: inline-block;
  border-style: solid;
  appearance: none;
  box-shadow: none;
  box-sizing: border-box;
  line-height: normal;
  border-color: transparent;
  border-width: 1px;
  font-size: 1rem;
  transition: background 0.2s ease;
  cursor: pointer;
}

.btn-primary {
  background-color: #025f63;
  border-color: #025f63;
  color: white;
}
.btn-primary:hover, .btn-primary:focus {
  background-color: #026d72;
}
.btn-primary:active {
  background-color: #025154;
}
.btn-secondary {
  background-color: #ff8283;
  border-color: #ff8283;
  color: black;
}
.btn-secondary:hover, .btn-secondary:focus {
  background-color: #ff9192;
}
.btn-secondary:active {
  background-color: #ff7374;
}
.btn-success {
  background-color: #127039;
  border-color: #127039;
  color: white;
}
.btn-success:hover, .btn-success:focus {
  background-color: #147d40;
}
.btn-success:active {
  background-color: #106332;
}
.btn-warning {
  background-color: #c2b542;
  border-color: #c2b542;
  color: black;
}
.btn-warning:hover, .btn-warning:focus {
  background-color: #c6ba4e;
}
.btn-warning:active {
  background-color: #b9ac3c;
}
.btn-error {
  background-color: #d05353;
  border-color: #d05353;
  color: white;
}
.btn-error:hover, .btn-error:focus {
  background-color: #d35f5f;
}
.btn-error:active {
  background-color: #cd4747;
}
.btn-dark {
  background-color: #111111;
  border-color: #111111;
  color: white;
}
.btn-dark:hover, .btn-dark:focus {
  background-color: #191919;
}
.btn-dark:active {
  background-color: #090909;
}
.btn-light {
  background-color: #f5f5f5;
  border-color: #f5f5f5;
  color: black;
}
.btn-light:hover, .btn-light:focus {
  background-color: #fdfdfd;
}
.btn-light:active {
  background-color: #ededed;
}
.btn-gray {
  background-color: #88a0a8;
  border-color: #88a0a8;
  color: black;
}
.btn-gray:hover, .btn-gray:focus {
  background-color: #91a7ae;
}
.btn-gray:active {
  background-color: #7f99a2;
}

[data-theme=light] {
  --shade-0: #f2f3f4;
  --shade-1: #e3e3e6;
  --shade-2: rgba(0, 0, 0, 0.2);
