body {
  margin: 0;
  padding: 0;
  font-family: "Pretendard", "Noto Sans KR", -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI";
}

.btns > .btn {
  margin: 0.25rem 0.25rem;
}

.effect {
  perspective: 800px;
}
.effect--child {
  transform: rotateX(10deg) rotateY(-18deg) rotateZ(3deg);
  transition: 0.3s;
}
.effect--child:hover, .effect--child:focus {
  transform: rotate(0);
}

.anim-bg-gradient {
  background: linear-gradient(120deg, #0044ff, #00d9ff, #ff7300);
  background-size: 200% 100%;
  background-position: 100% 0;
  transition: background-position 0.5s;
  border: none;
  color: black;
}
.anim-bg-gradient:hover, .anim-bg-gradient:focus {
  background-position: 0 0;
  color: black;
}

.anim-underline-fx {
  background-image: linear-gradient(to right, #0044ff 50%, #00ffbf 50%);
  background-size: 200% 4px;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  transition: background-position 0.3s;
}
.anim-underline-fx:hover, .anim-underline-fx:focus {
  background-position: 0% 100%;
}

.text-gradient {
  font-size: 4em;
  font-weight: bold;
  text-align: center;
}
@supports (--css: variables) {
  .text-gradient {
    background: linear-gradient(to right, darkblue, darkorchid);
    color: transparent;
    background-clip: text;
  }
}
.text-gradient::selection {
  color: white;
  background: darkorchid;
}

.scroll-snap {
  --scroll-gap: 1em;
  overflow: auto;
  padding: var(--scroll-gap) 0;
  scroll-snap-type: y mandatory;
}
.scroll-snap__child {
  scroll-snap-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(100% - 3 * var(--scroll-gap));
  border-radius: 0.4em;
  padding: 1.25em;
  box-shadow: 0 0.9px 1.5px rgba(0, 0, 0, 0.03), 0 3.1px 5.5px rgba(0, 0, 0, 0.08), 0 14px 25px rgba(0, 0, 0, 0.12);
}
.scroll-snap__child:not(:last-child) {
  margin-bottom: var(--scroll-gap);
}
.scroll-snap__child:nth-child(1) {
  background-color: #0044ff;
}
.scroll-snap__child:nth-child(2) {
  background-color: #25bbe0;
}
.scroll-snap__child:nth-child(3) {
  background-color: #88ff00;
}
.scroll-snap__child:nth-child(4) {
  background-color: #ffe600;
}
.scroll-snap__child:nth-child(5) {
  background-color: #ff6600;
}

.display-1,
.display-2,
.display-3,
.display-4 {
  font-family: "Yeseva One";
}

.box-enter {
  opacity: 0;
  transform: scale(0.6);
}

.box-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 1000ms;
}

.box-exit {
  opacity: 1;
  transform: scale(1);
}

.box-exit-active {
  opacity: 0;
  transform: scale(0.6);
  transition: all 1000ms;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}