.dialog-backdrop {
  z-index: 40;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(5px);
}

.dialog {
  z-index: 50;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;
  box-sizing: border-box;
}
.dialog .dialog-content {
  position: relative;
  background-color: #111111;
  margin: 0 auto;
  width: auto;
  min-height: 200px;
}

.dialogs-enter .dialog-content {
  opacity: 0;
  transform: scale(0.9);
}

.dialogs-enter-active .dialog-content {
  opacity: 1;
  transform: translateX(0);
  transition: all 0.3s;
}

.dialogs-exit .dialog-content {
  opacity: 1;
}

.dialogs-exit-active .dialog-content {
  opacity: 0;
  transform: scale(0.9);
  transition: all 0.3s;
}

.dialogs-exit-active .dialog-backdrop {
  opacity: 0;
  transition: all 0.3s;
}